import { Component, Input, Sanitizer } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'app-view-file',
  templateUrl: './view-file.component.html',
  styleUrls: ['./view-file.component.scss']
})
export class ViewFileComponent {
  @Input() imageUploadedView: any;
  viewOnly = false;
  fileType = '';
  fileURL = '';
  constructor(
    public activeModal: NgbActiveModal,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit(): void {
    console.log(this.imageUploadedView);
    this.viewOnly = this.imageUploadedView?.viewOnly;
    if (this.viewOnly) {
      this.fileType = this.getFileType(this.imageUploadedView.url);
      this.fileURL = this.imageUploadedView.url;
    }
  }
  viewPdf(file) {
    let src = URL.createObjectURL(file);
    return this.sanitizer.bypassSecurityTrustResourceUrl(src)
  }
  getFileType(fileName) {
    return fileName?.split('.').pop().trim().toLowerCase();
  }
}
