import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SharedService } from '../../../../../services/shared.service';
@Injectable({
  providedIn: 'root'
})
export class UnsettledSessionService {
  apiUrl = this.sharedService.getUrl;
  constructor(private http: HttpClient, public sharedService: SharedService) { }
  getRegisterDirectCashDeposit(sageAccountID): Observable<any> {
    let endPoint = `AllocationOrdersService/GetRegisterDirectCashDeposit?sageAccountID=` + sageAccountID;
    return this.http.get<any>(
      this.apiUrl + endPoint,
      this.sharedService.getHeaders()
    );
  }
  getCashDeposits(filters): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `AllocationOrdersService/GetDeposits${filters}`,
      this.sharedService.getHeaders()
    );
  }
  getBanks(): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `BankAccount/GetBanks?pageNo=0&status=2001&pageSize=1000`,
      this.sharedService.getHeaders()
    );
  }
  getDataFromLookUp(param): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Lookups/GetLookups${param}`,
      this.sharedService.getHeaders()
    );
  }
  getBankAccounts(param): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `BankAccount/GetBankAccounts${param}`,
      this.sharedService.getHeaders()
    );
  }
  getRegisterDirectDepositCheques(sageAccountID,registerId): Observable<any> {
    let endPoint = `AllocationOrdersService/GetRegisterDirectDepositCheques?sageAccountID=${sageAccountID}&registerId=${registerId}`;
    return this.http.get<any>(
      this.apiUrl + endPoint,
      this.sharedService.getHeaders()
    );
  }
  getRegisterUnsettledCheques(params): Observable<any> {
    let endPoint = `AllocationOrdersService/GetRegisterUnsettledCheques${params}`;
    return this.http.get<any>(
      this.apiUrl + endPoint,
      this.sharedService.getHeaders()
    );
  }
  getRegisterUnsettledSessions(params: string): Observable<any> {
    let endPoint = `Sessions/GetRegisterUnsettledSessions` + params;
    return this.http.get<any>(
      this.apiUrl + endPoint,
      this.sharedService.getHeaders()
    );
  }
  getRegisterUnsettledSessionsAmount(registerId): Observable<any> {
    let endPoint = `Sessions/GetRegisterUnsettledSessionsAmount?registerId=${registerId}`;
    return this.http.get<any>(
      this.apiUrl + endPoint,
      this.sharedService.getHeaders()
    );
  }
  getSageBanksAccounts(registerId): Observable<any> {
    let endPoint = `Register/GetRegisterDetails?Id=${registerId}`;
    return this.http.get<any>(
      this.apiUrl + endPoint,
      this.sharedService.getHeaders()
    );
  }

  registerDirectDepositCheques(params): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + `AllocationOrdersService/RegisterDirectDepositCheques`, params,
      this.sharedService.getHeaders()
    );
  }
  registerDirectCashDeposite(params): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + `AllocationOrdersService/RegisterDirectCashDeposite`, params,
      this.sharedService.getHeaders()
    );
  }
  getUserCashier(): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `User/GetUsers?registerId=${this.sharedService.getRegister?.registerObj?.id}&RoleId=1&sort=1&Status=2001&Invitation=2004&PageNo=0&PageSize=1000`,

      this.sharedService.getHeaders()
    );
  }

}
